import React, { useState, useEffect } from 'react'
import { DataPoint, Indicator } from '../../types'

interface DataOverviewProps {
  selectedIndicator: string;
  indicators: Indicator[];
}

const DataOverview: React.FC<DataOverviewProps> = ({ selectedIndicator }) => {
  const [latestData, setLatestData] = useState<DataPoint[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true)
        
        // 尝试从后端API获取数据
        const overviewResponse = await fetch('/api/overview')
        if (overviewResponse.ok) {
          const responseData = await overviewResponse.json()
          const latestDataPoints: DataPoint[] = responseData.data?.latestData || []
          if (latestDataPoints.length > 0) {
            setLatestData(latestDataPoints)
            setLoading(false)
            return
          }
        }
        
        // API调用失败或无数据时，使用模拟数据作为后备
        throw new Error('API调用失败，使用模拟数据')
        
      } catch (error) {
        console.error('Error fetching data from API:', error)
        
        // 使用模拟数据作为后备方案
        const mockData: DataPoint[] = [
          {
            id: '1',
            indicatorId: 'PCE',
            releaseDate: '2024-01-15',
            actualValue: 3.2,
            expectedValue: 3.0,
            previousValue: 3.1,
            impactLevel: 'high',
            changeDirection: 'up',
            changeAmount: 0.1
          },
          {
            id: '2',
            indicatorId: 'NFP',
            releaseDate: '2024-01-05',
            actualValue: 216000,
            expectedValue: 175000,
            previousValue: 173000,
            impactLevel: 'high',
            changeDirection: 'up',
            changeAmount: 41000
          },
          {
            id: '3',
            indicatorId: 'CPI',
            releaseDate: '2024-01-10',
            actualValue: 3.4,
            expectedValue: 3.2,
            previousValue: 3.3,
            impactLevel: 'medium',
            changeDirection: 'up',
            changeAmount: 0.1
          },
          {
            id: '4',
            indicatorId: 'UNEMPLOYMENT',
            releaseDate: '2024-01-05',
            actualValue: 3.7,
            expectedValue: 3.8,
            previousValue: 3.8,
            impactLevel: 'medium',
            changeDirection: 'down',
            changeAmount: -0.1
          },
          {
            id: '5',
            indicatorId: 'GDP',
            releaseDate: '2024-01-25',
            actualValue: 2.4,
            expectedValue: 2.2,
            previousValue: 2.1,
            impactLevel: 'high',
            changeDirection: 'up',
            changeAmount: 0.3
          }
        ]
        
        console.log('Using fallback mock data:', mockData)
        setLatestData(mockData)
        setLoading(false)
      }
    }
    
    fetchData()
  }, [])

  const getDirectionIcon = (direction: string) => {
    switch (direction) {
      case 'up': return '↗'
      case 'down': return '↘'
      case 'stable': return '→'
      default: return '→'
    }
  }

  if (loading) {
    return (
      <div className="card">
        <h2 className="text-xl font-semibold mb-4">数据概览</h2>
        <div className="animate-pulse">
          <div className="h-4 bg-gray-200 rounded mb-2"></div>
          <div className="h-4 bg-gray-200 rounded mb-2"></div>
          <div className="h-4 bg-gray-200 rounded"></div>
        </div>
      </div>
    )
  }

  // Filter data based on selected indicator
  const filteredData = selectedIndicator 
    ? latestData.filter(data => data.indicatorId === selectedIndicator)
    : []
  
  console.log('Selected indicator:', selectedIndicator)
  console.log('Latest data:', latestData)
  console.log('Filtered data:', filteredData)

  // 获取下次数据发布信息
  const getNextDataInfo = () => {
    const nextDataMap: Record<string, { date: string; time: string; description: string }> = {
      PCE: {
        date: '2024-02-29',
        time: '21:30',
        description: '1月PCE物价指数将公布，市场关注通胀走势'
      },
      NFP: {
        date: '2024-02-02', 
        time: '21:30',
        description: '1月非农就业数据将公布，预期新增就业17.5万人'
      },
      CPI: {
        date: '2024-02-13',
        time: '21:30', 
        description: '1月CPI数据将公布，预期同比3.2%'
      },
      UNEMPLOYMENT: {
        date: '2024-02-02',
        time: '21:30',
        description: '1月失业率数据将公布，预期维持3.7%'
      },
      GDP: {
        date: '2024-04-25',
        time: '20:30',
        description: 'Q1 GDP初值将公布，预期环比增长2.0%'
      }
    }
    return nextDataMap[selectedIndicator] || null
  }

  const nextData = getNextDataInfo()

  return (
    <div className="card">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-xl font-semibold">数据概览</h2>
        <div className="text-sm text-gray-600">
          当前指标: {selectedIndicator}
        </div>
      </div>

      {/* 最新数据 */}
      <div className="mb-6">
        <h3 className="text-lg font-medium mb-3">最新数据 <span className="text-sm text-gray-500 font-normal">（截止最近一次公布）</span></h3>
        <div className="space-y-4">
          {filteredData.map((data) => {
            return (
              <div key={data.id} className="border rounded-lg p-4">
                
                <div className="grid grid-cols-3 gap-4 text-sm">
                  <div>
                    <p className="text-gray-500">实际值</p>
                    <p className="font-medium text-lg">{data.actualValue} {getDirectionIcon(data.changeDirection)}</p>
                  </div>
                  <div>
                    <p className="text-gray-500">预期值</p>
                    <p className="font-medium text-lg">{data.expectedValue}</p>
                  </div>
                  <div>
                    <p className="text-gray-500">前值</p>
                    <p className="font-medium text-lg">{data.previousValue}</p>
                  </div>
                </div>
                
                <div className="mt-4 p-3 bg-gray-50 rounded">
                  <p className="text-sm text-gray-700 font-medium">市场影响分析：</p>
                  <p className="text-sm text-gray-700 mt-1">
                    {data.indicatorId === 'PCE' && 'PCE超预期上涨，通胀压力加大，股市可能承压，债券收益率上升。'}
                    {data.indicatorId === 'NFP' && '非农就业超预期，就业市场强劲，利好股市，但可能延缓降息预期。'}
                    {data.indicatorId === 'CPI' && 'CPI同比上涨，通胀压力仍存，可能影响美联储降息节奏。'}
                    {data.indicatorId === 'UNEMPLOYMENT' && '失业率低于预期，就业市场稳健，支撑消费支出，利好经济增长。'}
                    {data.indicatorId === 'GDP' && 'GDP超预期增长，经济动能强劲，股市受益，美元走强。'}
                  </p>
                </div>
                
                <div className="mt-3 text-xs text-gray-500">
                  发布日期：{new Date(data.releaseDate).toLocaleDateString('zh-CN')}
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* 下次数据 */}
      {nextData && (
        <div>
          <h3 className="text-lg font-medium mb-3">下次数据</h3>
          <div className="bg-orange-50 p-4 rounded-lg border-l-4 border-orange-400">
            <div className="flex justify-between items-start mb-2">
              <p className="text-orange-800 font-medium">{selectedIndicator}</p>
              <span className="text-orange-600 text-sm font-medium">{nextData.date} {nextData.time}</span>
            </div>
            <p className="text-orange-700 text-sm">{nextData.description}</p>
          </div>
        </div>
      )}
    </div>
  )
}

export default DataOverview